$font-m: Montserrat;
$font-l: Lato;

.wrapper {
  position: relative;
}

.container {
  width: 290px;
  margin: 0 auto;
  position: relative;
}
@media (min-width: 480px) {
  .container {
    width: 450px;
  }
}
@media (min-width: 640px) {
  .container {
    width: 610px;
  }
}
@media (min-width: 718px) {
  .container {
    width: 768px;
  }
}
@media (min-width: 1024px) {
  .container {
    width: 994px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1020;
  }
}
